html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  color: #fff;
  background: linear-gradient(50deg, #020024 0%, #097978 0%, #00d4ff 100%);
  font-family: Lato, sans-serif;
}

.container {
  max-width: 600px;
  flex-direction: column;
  justify-content: center;
  margin: 30px auto 150px;
  display: flex;
}

.main-container {
  background-color: #081229;
  border-radius: 6px;
  margin: 10px;
  padding: 50px;
  box-shadow: 0 18px 50px -10px #0003;
}

.main-container .information {
  letter-spacing: 2px;
  margin-bottom: 30px;
  font-size: 14px;
}

.main-container .footer {
  text-align: center;
  padding-top: 30px;
}

@media (max-width: 500px) {
  .main-container {
    padding: 30px 15px;
  }

  .main-container .information {
    font-size: 11px;
  }

  .main-container .footer {
    font-size: 12px;
  }
}

.header {
  padding: 20px;
}

.header__heading {
  text-align: center;
  letter-spacing: 1px;
  word-spacing: 3px;
  padding: 20px;
  font-size: 40px;
}

@media (max-width: 500px) {
  .header__heading {
    font-size: 25px;
  }
}

.cards__collection {
  height: 100px;
  margin: 0 auto;
}

.card {
  text-align: left;
  text-shadow: 0 1px 1px #0009;
  background: linear-gradient(50deg, #020024 0%, #097978 0%, #00d4ff 100%);
  border-radius: 20px;
  margin-bottom: 50px;
  padding: 20px;
  font-family: ocr-a-std, monospace;
  position: relative;
}

.card__header {
  justify-content: space-between;
  display: flex;
}

.card__header-information {
  padding-bottom: 15px;
  font-size: 20px;
}

.card__header-contactless {
  height: 40px;
}

.card__chip {
  height: 70px;
  width: 70px;
  padding-left: 10px;
}

.card__number {
  text-align: center;
  letter-spacing: 1px;
  padding: 20px;
  font-size: 30px;
  font-weight: bold;
}

.card__number--correct {
  color: orange;
}

.card__dates {
  text-transform: uppercase;
  gap: 15px;
  padding-left: 10px;
  display: flex;
}

.card__dates__expire {
  text-align: center;
  padding-bottom: 20px;
  font-size: 14px;
}

.card__dates__expire-period:after {
  content: "\a ";
  white-space: pre;
}

.card__footer {
  justify-content: space-between;
  display: flex;
}

.card__holder {
  margin: 0;
  font-size: 20px;
}

.card__logo {
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.card__logo--mastercard {
  height: 60px;
  margin: 5px 5px 0 0;
}

.card__logo--visa {
  height: 40px;
  margin-bottom: 10px;
}

.card__logo--americanExpress {
  height: 70px;
  margin-left: 20px;
}

@media (max-width: 500px) {
  .card__header-contactless {
    height: 25px;
  }

  .card__chip {
    height: 40px;
    width: 40px;
    padding-left: 10px;
  }

  .card__number {
    padding: 10px;
    font-size: 20px;
  }

  .card__dates__expire {
    font-size: 10px;
  }

  .card__holder {
    font-size: 15px;
  }

  .card__logo--mastercard {
    height: 40px;
  }

  .card__logo--visa {
    height: 20px;
    margin-bottom: 10px;
  }

  .card__logo--americanExpress {
    height: 30px;
  }
}

.form {
  flex-direction: column;
  display: flex;
}

.form__label {
  letter-spacing: 2px;
  font-size: 18px;
}

.form__numberInput {
  margin-top: 20px;
  padding: 15px;
  font-size: 18px;
}

.form__list {
  height: 200px;
  margin: 0;
  padding: 30px 0;
  list-style: none;
}

.form__list-error {
  color: #dc143c;
  margin: 0;
  padding: 5px 0;
  font-size: 16px;
}

.form__button {
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  background-color: orange;
  border: none;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  padding: 20px 30px;
  font-size: 18px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 10px 36px #00000029, 0 0 0 1px #0000000f;
}

.form__button:hover {
  font-weight: 700;
  transform: scale(1.05);
}

@media (max-width: 500px) {
  .form__label {
    font-size: 14px;
  }

  .form__button {
    padding: 15px 30px;
    font-size: 15px;
  }

  .form__list {
    height: 190px;
  }

  .form__list-error {
    font-size: 12px;
  }
}

